





import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import RepositoryEditor from "@/components/misc/RepositoryEditor.vue";
import { Overwrite } from "@/shared";
import { IRepository } from "@/store/modules/RepositoriesModule";
import { repositoriesModule } from "@/store/modules";

@Component({
  components: {
    RepositoryEditor
  }
})
export default class NewRepositoryView extends Mixins() {
  async onSubmit(
    repo: Overwrite<
      IRepository,
      {
        id?: number;
      }
    >
  ) {
    // TODO: infer args from other class
    // TODO: trycatch
    const resp = await repositoriesModule.createRepository(repo);
    if (resp && resp.payload) {
      this.$router.replace({
        path: `/repositories/${resp.payload.repo.id}`
      });
    }
  }
}
